        ä¼šè¯è®°å½•
      </h3>
      <div class="flex items-center space-x-2">
        <span class="text-xs text-gray-500">{{ totalSessions }} ä¸ªä¼šè¯</span>
        <button
          @click="refreshSessions"
          :disabled="isRefreshing"
          class="text-xs text-primary-600 hover:text-primary-700 disabled:opacity-50"
        >
          {{ isRefreshing ? 'åˆ·æ–°ä¸­...' : 'åˆ·æ–°' }}
        </button>
      </div>
    </div>
    
    <!-- ä¼šè¯åˆ—è¡¨ -->
    <div class="space-y-3 max-h-96 overflow-y-auto">
      <div
        v-for="session in displaySessions"
        :key="session.session_id"
        class="session-item p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors cursor-pointer"
        @click="selectSession(session)"
      >
        <div class="flex items-start justify-between mb-2">
          <div class="flex items-center space-x-2">
            <div :class="['w-2 h-2 rounded-full', getSessionStatusColor(session)]"></div>
            <span class="font-medium text-gray-900 text-sm">
              ä¼šè¯ {{ session.session_id.substring(0, 8) }}...
            </span>
          </div>
          
          <div class="flex items-center space-x-3 text-xs text-gray-500">
            <span>{{ formatDuration(session.duration) }}</span>
            <span>{{ formatTime(session.start_time) }}</span>
          </div>
        </div>
        
        <!-- ç”¨æˆ·è¾“å…¥ -->
        <div class="mb-2">
          <div class="text-xs font-medium text-gray-600 mb-1">ç”¨æˆ·:</div>
          <div class="text-sm text-gray-800 bg-white rounded px-2 py-1">
            {{ truncateText(session.user_input, 60) }}
          </div>
        </div>
        
        <!-- AIå›å¤ -->
        <div class="mb-3">
          <div class="text-xs font-medium text-gray-600 mb-1">AI:</div>
          <div class="text-sm text-gray-800 bg-white rounded px-2 py-1">
            {{ truncateText(session.ai_response, 80) }}
          </div>
        </div>
        
        <!-- ç»Ÿè®¡ä¿¡æ¯ -->
        <div class="flex items-center justify-between text-xs">
          <div class="flex items-center space-x-4">
            <span class="flex items-center space-x-1">
              <CheckCircleIcon class="w-3 h-3 text-green-500" />
              <span class="text-gray-600">{{ session.success_count }}</span>
            </span>
            <span class="flex items-center space-x-1">
              <XCircleIcon class="w-3 h-3 text-red-500" />
              <span class="text-gray-600">{{ session.failed_count }}</span>
            </span>
          </div>
          
          <ChevronRightIcon class="w-4 h-4 text-gray-400" />
        </div>
      </div>
      
      <!-- ç©ºçŠ¶æ€ -->
      <div v-if="!hasData" class="text-center py-8 text-gray-500">
        <ChatBubbleLeftRightIcon class="w-12 h-12 mx-auto mb-2 opacity-50" />
        <p class="text-sm">æš‚æ— ä¼šè¯è®°å½•</p>
      </div>
      
      <!-- åŠ è½½æ›´å¤š -->
      <div v-if="hasMore" class="text-center pt-4">
        <button
          @click="loadMore"
          :disabled="isLoadingMore"
          class="text-sm text-primary-600 hover:text-primary-700 disabled:opacity-50"
        >
          {{ isLoadingMore ? 'åŠ è½½ä¸­...' : 'åŠ è½½æ›´å¤š' }}
        </button>
      </div>
    </div>
  </div>
  
  <!-- ä¼šè¯è¯¦æƒ…æ¨¡æ€æ¡† -->
  <SessionDetailModal
    v-if="selectedSession"
    :session="selectedSession"
    @close="selectedSession = null"
  />
