<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import { useMonitoringStore } from '@/stores/monitoring'
import { formatRelativeTime, truncateText } from '@/utils/formatters'
import type { SessionData } from '@/types'
import {
  ChatBubbleLeftRightIcon,
  CheckCircleIcon,
  XCircleIcon,
  ChevronRightIcon
} from '@heroicons/vue/24/outline'
import SessionDetailModal from './SessionDetailModal.vue'

const monitoringStore = useMonitoringStore()

const isRefreshing = ref(false)
const isLoadingMore = ref(false)
const selectedSession = ref<SessionData | null>(null)
const displayLimit = ref(10)

// æ¨¡æ‹Ÿä¼šè¯æ•°æ®
const mockSessions = ref<SessionData[]>([
  {
    session_id: 'session_1234567890abcdef',
    start_time: new Date(Date.now() - 300000).toISOString(),
    duration: 2.5,
    success_count: 12,
    failed_count: 0,
    user_input: 'è¯·å¸®æˆ‘åˆ†æä¸€ä¸‹ç³»ç»Ÿçš„æ€§èƒ½ç“¶é¢ˆåœ¨å“ªé‡Œï¼Ÿ',
    ai_response: 'æ ¹æ®ç›‘æ§æ•°æ®åˆ†æï¼Œç³»ç»Ÿä¸»è¦ç“¶é¢ˆåœ¨æ•°æ®åº“æŸ¥è¯¢å’Œç¼“å­˜å‘½ä¸­ç‡æ–¹é¢...'
  },
  {
    session_id: 'session_2345678901bcdefg',
    start_time: new Date(Date.now() - 600000).toISOString(),
    duration: 1.8,
    success_count: 8,
    failed_count: 1,
    user_input: 'å¦‚ä½•ä¼˜åŒ–è®°å¿†æ£€ç´¢çš„é€Ÿåº¦ï¼Ÿ',
    ai_response: 'å¯ä»¥é€šè¿‡ä»¥ä¸‹å‡ ä¸ªæ–¹é¢æ¥ä¼˜åŒ–è®°å¿†æ£€ç´¢é€Ÿåº¦ï¼š1. ä½¿ç”¨å‘é‡ç´¢å¼•...'
  },
  {
    session_id: 'session_3456789012cdefgh',
    start_time: new Date(Date.now() - 900000).toISOString(),
    duration: 3.2,
    success_count: 15,
    failed_count: 0,
    user_input: 'ç³»ç»Ÿçš„ç¼“å­˜å‘½ä¸­ç‡ä¸ºä»€ä¹ˆè¿™ä¹ˆé«˜ï¼Ÿ',
    ai_response: 'ç³»ç»Ÿé‡‡ç”¨äº†å¤šçº§ç¼“å­˜ç­–ç•¥ï¼ŒåŒ…æ‹¬ç»Ÿä¸€ç¼“å­˜ç®¡ç†å™¨å’Œ588å€åŠ é€ŸæŠ€æœ¯...'
  }
])

// è®¡ç®—å±æ€§
const sessions = computed(() => {
  return monitoringStore.sessions.length > 0 ? monitoringStore.sessions : mockSessions.value
})

const displaySessions = computed(() => {
  return sessions.value.slice(0, displayLimit.value)
})

const totalSessions = computed(() => {
  return monitoringStore.totalSessions || sessions.value.length
})

const hasData = computed(() => sessions.value.length > 0)

const hasMore = computed(() => {
  return displayLimit.value < sessions.value.length
})

// æ–¹æ³•
const formatDuration = (duration: number): string => {
  if (duration < 1) {
    return `${Math.round(duration * 1000)}ms`
  }
  return `${duration.toFixed(1)}s`
}

const formatTime = (timeStr: string): string => {
  try {
    const time = new Date(timeStr)
    return formatRelativeTime(time)
  } catch (error) {
    return 'æœªçŸ¥æ—¶é—´'
  }
}

const getSessionStatusColor = (session: SessionData): string => {
  if (session.failed_count > 0) {
    return 'bg-red-500'
  } else if (session.success_count > 10) {
    return 'bg-green-500'
  } else {
    return 'bg-blue-500'
  }
}

const selectSession = (session: SessionData) => {
  selectedSession.value = session
}

const refreshSessions = async () => {
  if (isRefreshing.value) return
  
  try {
    isRefreshing.value = true
    
    // åˆ·æ–°ç›‘æ§æ•°æ®
    await monitoringStore.fetchDashboardData()
    
    // å¦‚æœæ²¡æœ‰çœŸå®æ•°æ®ï¼Œæ›´æ–°æ¨¡æ‹Ÿæ•°æ®
    if (monitoringStore.sessions.length === 0) {
      // æ·»åŠ æ–°çš„æ¨¡æ‹Ÿä¼šè¯
      const newSession: SessionData = {
        session_id: `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
        start_time: new Date().toISOString(),
        duration: Math.random() * 3 + 0.5,
        success_count: Math.floor(Math.random() * 15) + 5,
        failed_count: Math.floor(Math.random() * 2),
        user_input: 'è¿™æ˜¯ä¸€ä¸ªæ–°çš„æµ‹è¯•æŸ¥è¯¢...',
        ai_response: 'è¿™æ˜¯ç³»ç»Ÿç”Ÿæˆçš„å›å¤å†…å®¹...'
      }
      
      mockSessions.value.unshift(newSession)
      
      // ä¿æŒæœ€å¤š20ä¸ªä¼šè¯
      if (mockSessions.value.length > 20) {
        mockSessions.value = mockSessions.value.slice(0, 20)
      }
    }
    
  } finally {
    isRefreshing.value = false
  }
}

const loadMore = async () => {
  if (isLoadingMore.value) return
  
  try {
    isLoadingMore.value = true
    
    // æ¨¡æ‹ŸåŠ è½½å»¶è¿Ÿ
    await new Promise(resolve => setTimeout(resolve, 500))
    
    displayLimit.value += 10
    
  } finally {
    isLoadingMore.value = false
  }
}

onMounted(() => {
  // åˆå§‹åŒ–æ—¶å¦‚æœæ²¡æœ‰æ•°æ®ï¼Œä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
  if (!hasData.value) {
    console.log('ä½¿ç”¨æ¨¡æ‹Ÿä¼šè¯æ•°æ®')
  }
})
</script>

<style scoped>
